import Vue from 'vue'
import VueRouter from 'vue-router'
import ToolIndex from "../views/ToolIndex";
import AnnotationTool from "../views/AnnotationTool";
import VerifyTool from "../views/VerifyTool";
// import Capture from "../views/Capture";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'tool-index',
    component: ToolIndex
  },
  {
    path: '/tool',
    name: 'annotation-tool',
    component: AnnotationTool
  },
  {
    path: '/verify',
    name: 'verify-tool',
    component: VerifyTool
  },
  // {
  //   path: '/capture',
  //   name: 'capture',
  //   component: Capture
  // },
  {
    path: '*',
    redirect: '/'
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
