<template>
  <!-- header -->
  <div class="ts_header">
    <div class="ts_h1" style="margin-top: 0px; position: absolute; left: 7px;">
      <h1>
        <a href="javascript:;">Ko.AI OCR</a>
      </h1>
    </div>
    <div class="to_menu">
      <ul>
        <li>
          <a @click="fnGoBoundingBox" href="javascript:;" class="lk_obj" title="Bounding Box">
            <i></i>Bounding Box
          </a>
        </li>
        <li>
          <a @click="fnGoVerification" href="javascript:;" class="lk_veri" title="Verification">
            <i></i>Verification
          </a>
        </li>
      </ul>
    </div>
    <!-- 사용자 메뉴 -->
<!--    <div class="ts_util">-->
<!--      <a @click="fnGoCapture" href="javascript:;" class="user_capture" title="Capture">-->
<!--        <i></i>캡쳐-->
<!--      </a>-->
<!--    </div>-->
    <!-- // 사용자 메뉴 -->
  </div>
  <!-- // header -->
</template>

<script>
export default {
  name: 'Header',
  computed: {
    isIndexView () {
      return this.$route.name === 'tool-index'
    }
  },
  methods: {
    fnGoBoundingBox () {
      if (this.isIndexView) {
        this.$router.push('/tool')
      }

      this.EventBus.on('GO_BOUNDING_BOX_RESULT', (result) => {
        if (result) {
          this.$router.push('/tool')
        }
      })
      this.EventBus.emit('GO_BOUNDING_BOX')
    },
    fnGoVerification () {
      if (this.isIndexView) {
        this.$router.push('/verify')
      }

      this.EventBus.on('GO_VERIFICATION_RESULT', (result) => {
        if (result) {
          this.$router.push('/verify')
        }
      })
      this.EventBus.emit('GO_VERIFICATION')
    },
    fnGoCapture () {
      if (this.isIndexView) {
        this.$router.push('/capture')
      }

      this.EventBus.on('GO_CAPTURE_RESULT', (result) => {
        if (result) {
          this.$router.push('/capture')
        }
      })
      this.EventBus.emit('GO_CAPTURE')
    }
  },
  mounted () {}
}
</script>