<template>
  <div id="content-main">
    <div class="wrap_tool">
      <commonHeader></commonHeader>
      <div class="to_headersub">
        <div class="to_btn_wrap" style="float: left;">
          <button type="button" @click="fnOpenFiles" class="btn_to upload">파일 열기</button>
          <button type="button" @click="fnSaveVerifyResult" class="btn_to">저장</button>
          <input type="file" multiple id="files" accept="image/*,application/json" @change="fnImageLoaded" style="display:none;" />
        </div>
        <div class="to_dataset_name">
          <span v-if="datasetJson.info && datasetJson.info.name">데이터셋 명 : {{ datasetJson.info.name }}</span>
        </div>
      </div>
      <div class="inner">
        <!-- tool left -->
        <div class="tool_box left">
          <div data-simplebar class="upload_list">
            <ul>
							<li v-bind:key="index" v-for="(image, index) in imageList" @click="fnSelectImage(index)">
								<div class="upload_thumb">
									<div class="thumb_img" :class="image.selected ? 'current' : ''">
										<div class="thumb_img_brd hover"></div>
										<img :id="'image' + index" :src="image.imageUrl" />
									</div>
									<div class="thumb_name"><span :title="image.fileName">{{ image.fileName }}</span></div>
								</div>
							</li>
						</ul>
          </div>
        </div>
        <!-- // tool left -->

        <!-- tool center -->
        <div class="tool_box center">
          <div class="tool_head">
            <div class="head_util">
              <div class="tool_opt_list">
                <div class="option_head">
                  <em class="tool_option default" title="축소/확대 선택"></em>
                  <i class="blind">축소/확대 선택</i>
                </div>
                <ul class="tool_zoom_items">
                  <li>
                    <em class="tool_option plus" @click="fnZoomIn" title="확대"></em>
                    <i class="blind">확대</i>
                  </li>
                  <li>
                    <em class="tool_option minus" @click="fnZoomOut" title="축소"></em>
                    <i class="blind">축소</i>
                  </li>
                  <li>
                    <em class="tool_option reset" @click="fnInitScale" title="원래대로"></em>
                    <i class="blind">원래대로</i>
                  </li>
                </ul>
              </div>
              <!-- 활성화 active 클래스명 추가 -->
              <div class="tool_opt_move" :class="[!selectedImage.isCropped && zoomCount > 0 ? 'active' : '', isMoveMode ? 'on' : '']" @click="fnChangeModeMove" title="이동">
                <i class="blind">이동</i>
              </div>
            </div>
            <div class="head_file">
              <span>파일명</span>
              <input type="text" readonly name class="form_file_name" v-model="selectedImage.fileName" />
              <em>.{{ selectedImage.fileExtension }}</em>
            </div>
          </div>
          <div class="tool_body">
            <!-- 이미지 -->
            <div class="loading_container" v-show="imageLoading || saveLoading">
              <div class="loader"></div>
              <p style="margin-top: 10px;" v-if="imageLoading">이미지를 불러오는 중입니다...</p>
              <p style="margin-top: 10px;" v-else-if="saveLoading">데이터셋 저장 중입니다...</p>
            </div>
            <div id="canvasContainer" class="canvas_container">
              <canvas id="labelCanvas"></canvas>
            </div>
          </div>
        </div>
        <!-- // tool center -->

        <!-- tool right -->
        <div class="tool_box right verify">
          <div class="tool_label">
            <div class="label_tp">
              <div class="tool_label_tit">라벨 정보</div>
            </div>
            <div class="veri_result">
              <div class="result_cont">
                <textarea v-model="selectedImage.failReason" id="failReason" placeholder="Fail 사유를 입력해 주세요."></textarea>
              </div>
              <div class="result_tit">
                <button type="submit" @click="fnVerify(true)" class="btn_result">Pass</button>
                <button type="submit" @click="fnVerify(false)" class="btn_result">Fail</button>
              </div>
            </div>
            <div class="label_cont">
              <div data-simplebar class="label_list">
                <div class="dataset_info">
                  <pre>
										<code v-html="jsonString">
										</code>
									</pre>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- //tool right -->
      </div>
    </div>

    <div class="whole_dim" v-if="imageLoading || saveLoading"></div>
  </div>
</template>

<script>
import $ from 'jquery'
import FileSaver from "file-saver";
import Excel from "exceljs";

async function getJsonText(file) {
  return await loadJson(file);
}

function loadJson(file) {
  const reader = new FileReader();

  return new Promise((resolve, reject) => {
    reader.onerror = () => {
      reader.abort();
      reject(new DOMException("Loading error."));
    };

    reader.onload = (e) => {
      resolve(e.target.result);
    };
    reader.readAsText(file, "utf-8");
  });
}

export default {
  name: 'verify-tool',
  components: {},
  data () {
    return {
      directory: '',
      initCanvasWidth: 0,
      initCanvasHeight: 0,
      windowWidthRatio: 1.0,
      windowHeightRatio: 1.0,
      image: null,
      saveLoading: false,
      imageLoading: false,
      canvas: null,
      context: null,
      mouseIsDown: false,
      canvasInterval: null,
      rectangles: [],
      imageList: [],
      selectedImage: {},
      selectedImageIndex: -1,
      labelList: [],
      selectedLabel: {},
      selectedBbox: {},
      startX: 0,
      startY: 0,
      endX: 0,
      endY: 0,
      canvasScaleFactor: 1.1,
      scaleFactor: null,
      zoomCount: 0,
      zoomInExp: 3,
      zoomOutExp: -3,
      imageMoveX: 0,
      imageMoveY: 0,
      isMoveMode: false,
      datasetJson: {},
      jsonString: '',
      jsonFile: undefined
    }
  },
  methods: {
    fnOpenFiles () {
      document.getElementById('files').click()
    },
    fnVerify (isPassed) {
      let vm = this

      if (!isPassed && (!vm.selectedImage.failReason || vm.selectedImage.failReason.length < 1)) {
        vm.alert('사유를 입력해주세요.')
        $('#failReason').focus()
        return
      }

      if (isPassed) {
        vm.selectedImage.failReason = ''
      }

      vm.selectedImage.isVerified = true
      vm.selectedImage.isPassed = isPassed

      if ((vm.selectedImageIndex + 1) < vm.imageList.length) {
        vm.fnSelectImage(vm.selectedImageIndex + 1)
      }
    },
    fnSaveVerifyResult () {
      let vm = this
      if (!vm.imageList || vm.imageList.length < 1) {
        vm.alert('저장할 정보가 없습니다.')
        return
      }

      if (vm.imageList.length !== vm._.filter(vm.imageList, (image) => { return image.isVerified }).length) {
        vm.alert('검증이 완료되지 않은 이미지가 있습니다.')
        return
      }

      vm.saveLoading = true

      const workbook = new Excel.Workbook()
      const worksheet = workbook.addWorksheet('검증 결과')

      worksheet.getCell('A1').value = '데이터셋 명'
      worksheet.mergeCells('B1:D1')
      worksheet.getCell('D1').value = vm.datasetJson.info.name
      worksheet.mergeCells('B2:D2')
      worksheet.getCell('A2').value = 'JSON 파일 명'
      worksheet.getCell('D2').value = vm.jsonFile.substring(vm.jsonFile.lastIndexOf('/') + 1)

      worksheet.getRow(3).values = ['이미지 파일 명', '종류', 'Pass / Fail', 'Fail 사유']

      worksheet.columns = [
        { key: '이미지 파일 명' },
        { key: '종류' },
        { key: 'Pass / Fail' },
        { key: 'Fail 사유' }
      ]

      let headerStyle = {
        alignment: {horizontal: 'center'},
        fill: { type: 'pattern', pattern: 'lightGray' },
        font: { bold: true }
      }

      worksheet.getCell('A1').style = headerStyle
      worksheet.getCell('A2').style = headerStyle
      worksheet.getCell('A3').style = headerStyle
      worksheet.getCell('B3').style = headerStyle
      worksheet.getCell('C3').style = headerStyle
      worksheet.getCell('D3').style = headerStyle

      vm.imageList.forEach((image) => {
        worksheet.addRow({
          '이미지 파일 명': image.fileName + '.' + image.fileExtension,
          '종류': image.isCropped ? 'Crop' : 'Select',
          'Pass / Fail': image.isPassed ? 'Pass' : 'Fail',
          'Fail 사유': image.failReason
        })
      })

      const canvas = document.createElement('canvas')
      const ctx = canvas.getContext('2d')
      if (!ctx) {
        return
      }

      const maxColumnLengths = []
      let borderStyles = {
        top: { style: 'thin' },
        left: { style: 'thin' },
        bottom: { style: 'thin' },
        right: { style: 'thin' }
      }

      worksheet.eachRow((row, rowNum) => {
        if (rowNum < 0) {
          return
        }

        row.eachCell((cell, num) => {
          cell.border = borderStyles
          if (typeof cell.value === 'string') {
            if (maxColumnLengths[num] === undefined) {
              maxColumnLengths[num] = 0
            }

            const fontSize = cell.font && cell.font.size ? cell.font.size : 11
            ctx.font = `${fontSize}pt Arial`
            const metrics = ctx.measureText(cell.value)
            const cellWidth = metrics.width

            maxColumnLengths[num] = Math.max(maxColumnLengths[num], cellWidth)
          }
        })
      })

      for (let i = 1; i <= worksheet.columnCount; i++) {
        const col = worksheet.getColumn(i)
        const width = maxColumnLengths[i]
        if (width) {
          col.width = width / 7.5 + 1
        }
      }

      workbook.xlsx.writeBuffer()
        .then(function (data) {
          const blob = new Blob([data], {type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"});
          FileSaver.saveAs(blob, `${vm.datasetJson.info.name}.xlsx`);

          vm.saveLoading = false;
          vm.alert('저장 되었습니다.');
        })
        .catch((err) => {
          vm.saveLoading = false;
          vm.alert('저장 중 오류가 발생했습니다.\n오류 : ' + err)
        });
    },
    fnImageLoaded (e) {
      let vm = this

      vm.imageList = []
      vm.datasetJson = {}
      vm.jsonString = ''

      const selectedFiles = [...e.target.files];
      // selectedFiles.forEach(file => console.log(file.type));

      const jsonFiles = selectedFiles.filter(file => file.name.endsWith(".json"));
      if (!jsonFiles || jsonFiles.length < 1) {
        vm.alert('JSON 파일이 존재하지 않습니다.');
        return;
      } else if (jsonFiles.length > 1) {
        vm.alert('JSON 파일은 해당 폴더에 하나만 있어야 합니다.')
        return;
      }

      vm.jsonFile = jsonFiles[0].name;

      getJsonText(jsonFiles[0]).then(jText => {
        vm.jsonString = '';
        vm.datasetJson = JSON.parse(jText);
      });

      const imageFiles = selectedFiles.filter(file => !file.name.endsWith(".json"));
      vm.fnSetImageList(imageFiles);
    },
    fnSetImageList (files) {
      let vm = this

      let imageFile
      let fileExtension
      let imageUrl
      for (let i = 0; i < files.length; i++) {
        imageFile = files[i]
        fileExtension = files[i].name.substring(imageFile.name.lastIndexOf('.') + 1)
        imageUrl = URL.createObjectURL(files[i])
        vm.imageList.push({
          file: imageFile,
          newFileName: imageFile.name.substring(0, imageFile.name.lastIndexOf('.')),
          fileName: imageFile.name.substring(0, imageFile.name.lastIndexOf('.')),
          fileExtension: fileExtension,
          imageUrl: imageUrl,
          selected: false,
          isCropped: imageFile.name.lastIndexOf('_cropped_') > -1,
          isVerified: false,
          isPassed: false,
          failReason: ''
        })
      }
    },
    fnChangeModeMove () {
      let vm = this

      if (vm.selectedImage.isCropped) {
        return
      }

      // 이동 모드 전환
      if (vm.zoomCount < 1) {
        vm.isMoveMode = false
        $('#labelCanvas').css('cursor', 'default')
        return
      }

      vm.isMoveMode = !vm.isMoveMode

      vm.$forceUpdate()

      vm.$nextTick(() => {
        if (vm.isMoveMode) {
          $('#labelCanvas').css('cursor', '-webkit-grab')
        } else {
          $('#labelCanvas').css('cursor', 'default')
        }
      })
    },
    fnZoomIn () {
      let vm = this
      // canvas 확대
      if (Object.keys(vm.selectedImage).length < 1) {
        return
      }

      if (Math.abs(vm.zoomCount) > 4) {
        return
      }

      vm.zoomCount++
      vm.scaleFactor = Math.pow(vm.canvasScaleFactor, vm.zoomInExp)
      vm.context.clearRect(0, 0, vm.selectedImage.width, vm.selectedImage.height)

      if (vm.selectedImage.isCropped) {
        vm.canvas.width = vm.image.width * vm.scaleFactor
        vm.image.width = vm.image.width * vm.scaleFactor
        vm.canvas.height = vm.image.height * vm.scaleFactor
        vm.image.height = vm.image.height * vm.scaleFactor
        vm.context.drawImage(vm.image, 0, 0, vm.canvas.width, vm.canvas.height)

        let paddingTop = ($('.tool_body').height() / 2) - (vm.image.height / 2)
        $('#canvasContainer').css('padding-top', paddingTop + 'px')
      } else {
        vm.context.scale(vm.scaleFactor, vm.scaleFactor)
        vm.context.drawImage(vm.image, 0, 0, vm.image.width, vm.image.height, 0, 0, vm.canvas.width, vm.canvas.height)

        vm.fnDrawRectangle()
      }
    },
    fnZoomOut () {
      let vm = this
      // canvas 축소
      if (Object.keys(vm.selectedImage).length < 1) {
        return
      }

      if (vm.zoomCount === 0) {
        return
      }
      vm.zoomCount--
      vm.scaleFactor = Math.pow(vm.canvasScaleFactor, vm.zoomOutExp)
      vm.context.clearRect(0, 0, vm.selectedImage.width, vm.selectedImage.height)

      if (vm.selectedImage.isCropped) {
        vm.canvas.width = vm.image.width * vm.scaleFactor
        vm.image.width = vm.image.width * vm.scaleFactor
        vm.canvas.height = vm.image.height * vm.scaleFactor
        vm.image.height = vm.image.height * vm.scaleFactor
        vm.context.drawImage(vm.image, 0, 0, vm.canvas.width, vm.canvas.height)

        let paddingTop = ($('.tool_body').height() / 2) - (vm.image.height / 2)
        $('#canvasContainer').css('padding-top', paddingTop + 'px')
      } else {
        vm.context.scale(vm.scaleFactor, vm.scaleFactor)
        if (vm.zoomCount === 0) {
          vm.imageMoveX = 0
          vm.imageMoveY = 0
          vm.context.setTransform(1, 0, 0, 1, vm.imageMoveX, vm.imageMoveY)
          vm.fnChangeModeMove()
        }

        vm.context.drawImage(vm.image, 0, 0, vm.image.width, vm.image.height, 0, 0, vm.canvas.width, vm.canvas.height)

        vm.fnDrawRectangle()
      }
    },
    fnInitScale () {
      let vm = this
      // 확대/축소 원상태로 복구
      if (vm.zoomCount === 0) {
        return
      }

      vm.zoomCount = 0

      if (vm.selectedImage.isCropped) {
        vm.canvas.width = vm.selectedImage.realImage.naturalWidth
        vm.canvas.height = vm.selectedImage.realImage.naturalHeight
        vm.initCanvasWidth = vm.canvas.width
        vm.initCanvasHeight = vm.canvas.height
        vm.image.width = vm.canvas.width
        vm.image.height = vm.canvas.height

        let paddingTop = ($('.tool_body').height() / 2) - (vm.image.height / 2)
        $('#canvasContainer').css('padding-top', paddingTop + 'px')

        vm.context.drawImage(vm.image, 0, 0, vm.image.width, vm.image.height, 0, 0, vm.canvas.width, vm.canvas.height)
      } else {
        let isZoomed = vm.zoomCount > 0

        vm.imageMoveX = 0
        vm.imageMoveY = 0

        for (let i = 0; i < Math.abs(vm.zoomCount); i++) {
          vm.scaleFactor = Math.pow(vm.canvasScaleFactor, isZoomed ? vm.zoomOutExp : vm.zoomInExp)
          vm.context.scale(vm.scaleFactor, vm.scaleFactor)
        }

        vm.context.setTransform(1, 0, 0, 1, vm.imageMoveX, vm.imageMoveY)
        vm.context.clearRect(0, 0, vm.selectedImage.width, vm.selectedImage.height)
        vm.fnChangeModeMove()

        vm.context.drawImage(vm.image, 0, 0, vm.image.width, vm.image.height, 0, 0, vm.canvas.width, vm.canvas.height)

        vm.fnDrawRectangle()

        $('#canvasContainer').css('padding-top', '0')
      }
    },
    fnSetCanvasEvent () {
      let vm = this
      vm.canvas.addEventListener('mousedown', vm.fnMouseDown, false)
      vm.canvas.addEventListener('mousemove', vm.fnMouseMove, false)
      vm.canvas.addEventListener('mouseup', vm.fnMouseUp, false)
    },
    fnMouseDown () {
      let vm = this

      vm.mouseIsDown = true

      if (vm.isMoveMode) {
        $('#labelCanvas').css('cursor', '-webkit-grabbing')
      }
    },
    fnMouseMove (event) {
      let vm = this

      let isZoomed = vm.zoomCount > 0
      if (vm.mouseIsDown && vm.isMoveMode) {
        let moveScaleFactor = 0
        moveScaleFactor = Math.pow(vm.canvasScaleFactor, isZoomed ? vm.zoomInExp * vm.zoomCount : vm.zoomOutExp * Math.abs(vm.zoomCount))

        vm.imageMoveX += event.movementX
        vm.imageMoveY += event.movementY

        if (event.movementX > 0 && event.movementY > 0) {
          if (vm.imageMoveX > 0 && vm.imageMoveY > 0) {
            vm.imageMoveX = 0
            vm.imageMoveY = 0
            return
          }
        }

        if (event.movementX > 0 && vm.imageMoveX > 0) {
          vm.imageMoveX = 0
          return
        }
        if (event.movementY > 0 && vm.imageMoveY > 0) {
          vm.imageMoveY = 0
          return
        }

        let imageRealX = Math.round(vm.imageMoveX / ((moveScaleFactor - 1) * -1))
        let imageRealY = Math.round(vm.imageMoveY / ((moveScaleFactor - 1) * -1))

        if (vm.imageMoveX < 0 && vm.imageMoveY < 0) {
          if (imageRealX > vm.canvas.width && imageRealY > vm.canvas.height) {
            vm.imageMoveX = Math.round(vm.canvas.width * ((moveScaleFactor - 1) * -1))
            vm.imageMoveY = Math.round(vm.canvas.height * ((moveScaleFactor - 1) * -1))
            return
          }
        }
        if (imageRealX > vm.canvas.width) {
          vm.imageMoveX = Math.round(vm.canvas.width * ((moveScaleFactor - 1) * -1))
          return
        }
        if (imageRealY > vm.canvas.height) {
          vm.imageMoveY = Math.round(vm.canvas.height * ((moveScaleFactor - 1) * -1))
          return
        }

        vm.context.clearRect(0, 0, vm.selectedImage.width, vm.selectedImage.height)
        vm.context.setTransform(moveScaleFactor, 0, 0, moveScaleFactor, vm.imageMoveX, vm.imageMoveY)

        vm.context.drawImage(vm.image, 0, 0, vm.image.width, vm.image.height, 0, 0, vm.canvas.width, vm.canvas.height)

        vm.fnDrawRectangle()
      }
    },
    fnMouseUp () {
      let vm = this
      if (vm.mouseIsDown) {
        vm.mouseIsDown = false

        if (vm.isMoveMode) {
          $('#labelCanvas').css('cursor', '-webkit-grab')
        }
      }
    },
    fnSetCanvasSize () {
      let vm = this

      let canvasContainerWidth = $('#canvasContainer').width()
      let canvasContainerHeight = $('#canvasContainer').height()
      let ratio = 0

      if (canvasContainerWidth < vm.image.naturalWidth && canvasContainerHeight < vm.image.naturalHeight) {
        vm.selectedImage.biggerThanContainer = true
        // 이미지 가로x세로가 캔버스 가로x세로 보다 큰 경우
        if (vm.image.naturalWidth > vm.image.naturalHeight) {
          // 가로가 더 큰 경우
          ratio = parseFloat(canvasContainerWidth) / parseFloat(vm.image.naturalWidth)
          vm.canvas.width = Math.round(vm.image.naturalWidth * ratio) - 5
          vm.canvas.height = Math.round(vm.image.naturalHeight * ratio) - 5
          return
        } else if (vm.image.naturalHeight > vm.image.naturalWidth) {
          // 세로가 더 큰 경우
          ratio = parseFloat(canvasContainerHeight) / parseFloat(vm.image.naturalHeight)
          vm.canvas.width = Math.round(vm.image.naturalWidth * ratio) - 5
          vm.canvas.height = Math.round(vm.image.naturalHeight * ratio) - 5
          return
        }
      } else if (canvasContainerWidth < vm.image.naturalWidth) {
        vm.selectedImage.biggerThanContainer = true
        // 이미지 가로가 캔버스 가로 보다 큰 경우
        ratio = parseFloat(canvasContainerWidth) / parseFloat(vm.image.naturalWidth)
        vm.canvas.width = Math.round(vm.image.naturalWidth * ratio) - 5
        vm.canvas.height = Math.round(vm.image.naturalHeight * ratio) - 5
        return
      } else if (canvasContainerHeight < vm.image.naturalHeight) {
        vm.selectedImage.biggerThanContainer = true
        // 이미지 세로가 캔버스 세로 보다 큰 경우
        ratio = parseFloat(canvasContainerHeight) / parseFloat(vm.image.naturalHeight)
        vm.canvas.width = Math.round(vm.image.naturalWidth * ratio) - 5
        vm.canvas.height = Math.round(vm.image.naturalHeight * ratio) - 5
        return
      }

      if (canvasContainerWidth > vm.image.naturalWidth || canvasContainerHeight > vm.image.naturalHeight) {
        vm.selectedImage.biggerThanContainer = false
        // 이미지 가로x세로가 캔버스 가로x세로 보다 작은 경우
        let imageWidth = vm.image.naturalWidth
        let imageHeight = vm.image.naturalHeight

        while (imageWidth < canvasContainerWidth - 10 && imageHeight < canvasContainerHeight - 10) {
          imageWidth = imageWidth * 1.01
          imageHeight = imageHeight * 1.01
        }

        vm.canvas.width = imageWidth
        vm.canvas.height = imageHeight
      }
    },
    fnSelectImage (index) {
      let vm = this

      if (vm.selectedImageIndex === index) {
        return
      }

      vm.imageList.forEach((item) => {
        item.selected = false
      })
      vm.imageList[index].selected = true

      vm.selectedImageIndex = index
      vm.selectedImage = vm.imageList[index]
      vm.selectedImage.labelList = []

      vm.$forceUpdate()

      // Canvas 설정 (Image 포함)
      if (vm.canvas) {
        $(vm.canvas).off()
        vm.canvas = undefined
      }

      vm.canvas = $('#labelCanvas')[0]
      vm.context = vm.canvas.getContext('2d')

      vm.image = new Image()

      vm.image.onload = () => {
        vm.zoomCount = 0
        vm.selectedImage.realImage = vm.image

        vm.canvas.width = vm.selectedImage.realImage.naturalWidth
        vm.canvas.height = vm.selectedImage.realImage.naturalHeight

        $('#canvasContainer').css('padding-top', '0')
        if (vm.selectedImage.isCropped) {
          let paddingTop = ($('.tool_body').height() / 2) - (vm.image.height / 2)
          $('#canvasContainer').css('padding-top', paddingTop + 'px')
        } else {
          vm.fnSetCanvasSize()
        }

        let imageRatio = vm.fnGetRatio(vm.selectedImage)

        let selectedImageJson = vm._.find(vm.datasetJson.images, (image) => { return image.file_name === vm.selectedImage.file.name })
        let selectedImageId = '-1'
        if (selectedImageJson && selectedImageJson.id) {
          selectedImageId = selectedImageJson.id
        }
        let selectTypeLabels = vm._.filter(vm.datasetJson.annotations, (label) => { return label.image_id === selectedImageId })
        let groupedSelectTypeLabels = vm._.groupBy(selectTypeLabels, 'text')
        for (let text in groupedSelectTypeLabels) {
          let boundingBoxes = []
          groupedSelectTypeLabels[text].forEach((label) => {
            let boxX = vm.selectedImage.biggerThanContainer ? label.bbox[0] / imageRatio : label.bbox[0] * imageRatio
            let boxY = vm.selectedImage.biggerThanContainer ? label.bbox[1] / imageRatio : label.bbox[1] * imageRatio
            let boxWidth = vm.selectedImage.biggerThanContainer ? label.bbox[2] / imageRatio : label.bbox[2] * imageRatio
            let boxHeight = vm.selectedImage.biggerThanContainer ? label.bbox[3] / imageRatio : label.bbox[3] * imageRatio

            boundingBoxes.push({
              startX: boxX,
              startY: boxY,
              width: boxWidth,
              height: boxHeight
            })
          })

          vm.selectedImage.labelList.push({
            type: 'S',
            text: text,
            boundingBoxes: boundingBoxes
          })
        }

        /*
        let cropLabels = vm._.filter(vm.datasetJson.cropLabels, (label) => { return label.image_file_name === vm.selectedImage.file.name })
        cropLabels.forEach((label) => {
          let boxX = vm.selectedImage.biggerThanContainer ? label.bbox[0] / imageRatio : label.bbox[0] * imageRatio
          let boxY = vm.selectedImage.biggerThanContainer ? label.bbox[1] / imageRatio : label.bbox[1] * imageRatio
          let boxWidth = vm.selectedImage.biggerThanContainer ? label.bbox[2] / imageRatio : label.bbox[2] * imageRatio
          let boxHeight = vm.selectedImage.biggerThanContainer ? label.bbox[3] / imageRatio : label.bbox[3] * imageRatio

          vm.selectedImage.labelList.push({
            type: 'C',
            text: label.text,
            boundingBox: {
              startX: boxX,
              startY: boxY,
              width: boxWidth,
              height: boxHeight
            }
          })
        })
        */

        vm.initCanvasWidth = vm.canvas.width
        vm.initCanvasHeight = vm.canvas.height

        vm.context.drawImage(vm.image, 0, 0, vm.image.width, vm.image.height, 0, 0, vm.canvas.width, vm.canvas.height)

        vm.fnSetCanvasEvent()
        vm.fnDrawRectangle()
        vm.imageLoading = false
      }
      vm.imageLoading = true
      vm.$forceUpdate()
      vm.image.src = vm.imageList[index].imageUrl

      let jsonImages = []
      vm.datasetJson.images.forEach(function (jsonImage) {
        let selectedFileName = vm.selectedImage.fileName + '.' + vm.selectedImage.fileExtension
        if (selectedFileName === jsonImage.file_name) {
          jsonImages.push(jsonImage)
        }
      })

      vm.jsonString = '<br>images: ' + JSON.stringify(jsonImages, null, 4)

      let jsonLabels = []
      jsonImages.forEach(function (jsonImage) {
        let foundLabels = vm._.filter(vm.datasetJson.annotations, function (label) {
          return label.image_id === jsonImage.id
        })
        if (foundLabels) {
          jsonLabels.push(...foundLabels)
        }
      })

      vm.jsonString += '<br>annotations: ' + JSON.stringify(jsonLabels, null, 4)
    },
    fnDrawRectangle () {
      let vm = this

      if (vm.selectedImage.labelList && vm.selectedImage.labelList.length > 0) {
        vm.selectedImage.labelList.forEach(label => {
          if (label.type === 'S') {
            label.boundingBoxes.forEach(bbox => {
              if (bbox.selected) {
                vm.context.strokeStyle = '#f33'
              } else {
                vm.context.strokeStyle = '#111'
              }

              vm.context.setLineDash([])

              vm.context.beginPath()
              vm.context.rect(
                bbox.startX * vm.windowWidthRatio,
                bbox.startY * vm.windowHeightRatio,
                bbox.width * vm.windowWidthRatio,
                bbox.height * vm.windowHeightRatio
              )
              vm.context.closePath()
              vm.context.stroke()
            })
          } else {
            let bbox = label.boundingBox
            if (bbox.selected) {
              vm.context.strokeStyle = '#f33'
            } else {
              vm.context.strokeStyle = '#111'
            }

            vm.context.globalAlpha = 0.1
            vm.context.fillRect(
              bbox.startX * vm.windowWidthRatio,
              bbox.startY * vm.windowHeightRatio,
              bbox.width * vm.windowWidthRatio,
              bbox.height * vm.windowHeightRatio
            )
            vm.context.globalAlpha = 1.0
            vm.context.setLineDash([5, 3])

            vm.context.beginPath()
            vm.context.rect(
              bbox.startX * vm.windowWidthRatio,
              bbox.startY * vm.windowHeightRatio,
              bbox.width * vm.windowWidthRatio,
              bbox.height * vm.windowHeightRatio
            )
            vm.context.closePath()
            vm.context.stroke()
          }
        })
      }
    },
    fnSetCanvasResize () {
      let vm = this
      if (vm.selectedImage.canvas) {
        let resizedCanvasWidth = $(vm.selectedImage.canvas).width()
        let resizedCanvasHeight = $(vm.selectedImage.canvas).height()

        if (vm.initCanvasWidth < 0 && vm.initCanvasHeight < 0) {
          return
        }

        vm.windowWidthRatio = resizedCanvasWidth / vm.initCanvasWidth
        vm.windowHeightRatio = resizedCanvasHeight / vm.initCanvasHeight

        if (vm.image) {
          vm.fnSetCanvasSize()
          vm.selectedImage.context.drawImage(vm.image, 0, 0, vm.image.width, vm.image.height, 0, 0, vm.selectedImage.canvas.width, vm.selectedImage.canvas.height)
          vm.fnDrawRectangle()
        }
      }
    },
    fnGetRatio (image) {
      let canvasContainerWidth = $('#canvasContainer').width()
      let canvasContainerHeight = $('#canvasContainer').height()

      if (
        canvasContainerWidth < image.realImage.naturalWidth &&
        canvasContainerHeight < image.realImage.naturalHeight
      ) {
        if (image.realImage.naturalWidth > image.realImage.naturalHeight) {
          return (
            parseFloat(image.realImage.naturalWidth) /
            parseFloat(canvasContainerWidth)
          )
        } else if (
          image.realImage.naturalHeight > image.realImage.naturalWidth
        ) {
          return (
            parseFloat(image.realImage.naturalHeight) /
            parseFloat(canvasContainerHeight)
          )
        }
      } else if (canvasContainerWidth < image.realImage.naturalWidth) {
        return (
          parseFloat(image.realImage.naturalWidth) /
          parseFloat(canvasContainerWidth)
        )
      } else if (canvasContainerHeight < image.realImage.naturalHeight) {
        return (
          parseFloat(image.realImage.naturalHeight) /
          parseFloat(canvasContainerHeight)
        )
      }

      if (
        canvasContainerWidth > image.realImage.naturalWidth ||
        canvasContainerHeight > image.realImage.naturalHeight
      ) {
        let imageWidth = image.realImage.naturalWidth
        let imageHeight = image.realImage.naturalHeight
        let ratioCount = 0

        while (imageWidth < canvasContainerWidth - 10 && imageHeight < canvasContainerHeight - 10) {
          ratioCount++
          imageWidth = imageWidth * 1.01
          imageHeight = imageHeight * 1.01
        }

        let ratio = 1.01
        ratioCount = ratioCount - 1
        for (let i = 0; i < ratioCount; i++) {
          ratio = ratio * 1.01
        }

        return ratio
      }
    }
  },
  mounted () {
    let vm = this

    vm.EventBus.on('GO_BOUNDING_BOX', () => {
      if (vm.imageList && vm.imageList.length > 0) {
        let continueMove = vm.confirm('작업 중인 데이터가 모두 삭제됩니다. 이동 하시겠습니까?')
        vm.EventBus.emit('GO_BOUNDING_BOX_RESULT', continueMove)
      } else {
        vm.EventBus.emit('GO_BOUNDING_BOX_RESULT', true)
      }
    })
    vm.EventBus.on('GO_VERIFICATION', () => {
      vm.EventBus.emit('GO_VERIFICATION_RESULT', false)
    })
    vm.EventBus.on('GO_CAPTURE', () => {
      if (vm.imageList && vm.imageList.length > 0) {
        let continueMove = vm.confirm('작업 중인 데이터가 모두 삭제됩니다. 이동 하시겠습니까?')
        vm.EventBus.emit('GO_CAPTURE_RESULT', continueMove)
      } else {
        vm.EventBus.emit('GO_CAPTURE_RESULT', true)
      }
    })

    $('.label_tool_sel').hover(
      () => {
        if (Object.keys(vm.selectedImage).length < 1) {
          return
        }
        $('.label_tool_list').slideDown(100)
      },
      () => {
        $('.label_tool_list').slideUp(100)
      }
    )

    $('.tool_opt_list').hover(
      () => {
        if (Object.keys(vm.selectedImage).length < 1) {
          return
        }
        $('.tool_zoom_items').slideDown(100)
      },
      () => {
        $('.tool_zoom_items').slideUp(100)
      }
    )

    window.addEventListener('resize', () => {
      vm.fnSetCanvasResize()
    })

    window.addEventListener('maximize', () => {
      vm.fnSetCanvasResize()
    })

    window.addEventListener('unmaximize', () => {
      vm.fnSetCanvasResize()
    })

    window.addEventListener('minimize', () => {
      vm.fnSetCanvasResize()
    })

    window.addEventListener('restore', () => {
      vm.fnSetCanvasResize()
    })
  }
}
</script>

<style scoped>
.tool_body {height: calc(100% - 40px)}
</style>