<template>
  <div id="content-main">
    <div class="wrap_tool">
      <commonHeader></commonHeader>
      <div class="to_headersub">
        <div class="to_btn_wrap" style="float: left;">
          <button type="button" class="btn_to upload" @click="fnOpenFileDialog">파일 열기</button>
          <button type="button" class="btn_to" @click="fnOpenSavePoup">저장</button>
        </div>
      </div>

      <div class="inner">
        <input type="file" multiple="multiple" id="imageFiles" @change="fnImageLoaded" accept="image/*" style="display:none;" />
        <!-- tool left -->
        <div class="tool_box left">
          <div data-simplebar class="upload_list">
            <ul>
							<li v-bind:key="index" v-for="(image, index) in imageList" @click="fnSelectImage(index)">
								<div class="upload_thumb">
									<div class="thumb_img" :class="image.selected ? 'current' : ''">
										<div class="thumb_img_brd hover"></div>
										<img :id="'image' + index"/>
									</div>
									<div class="thumb_name"><span :title="image.fileName">{{ image.fileName }}</span></div>
								</div>
							</li>
						</ul>
          </div>
        </div>
        <!-- // tool left -->

        <!-- tool center -->
        <div class="tool_box center">
          <div class="tool_head">
            <div class="head_util">
              <div class="tool_opt_list">
                <div class="option_head">
                  <em class="tool_option default" title="축소/확대 선택"></em>
                  <i class="blind">축소/확대 선택</i>
                </div>
                <ul class="tool_zoom_items">
                  <li>
                    <em class="tool_option plus" @click="fnZoomIn" title="확대"></em>
                    <i class="blind">확대</i>
                  </li>
                  <li>
                    <em class="tool_option minus" @click="fnZoomOut" title="축소"></em>
                    <i class="blind">축소</i>
                  </li>
                  <li>
                    <em class="tool_option reset" @click="fnInitScale" title="원래대로"></em>
                    <i class="blind">원래대로</i>
                  </li>
                </ul>
              </div>
              <!-- 활성화 active 클래스명 추가 -->
              <div class="tool_opt_move" :class="[zoomCount > 0 ? 'active' : '', isMoveMode ? 'on' : '']" @click="fnChangeModeMove" title="이동">
                <i class="blind">이동</i>
              </div>
            </div>
            <div class="head_file">
              <span>파일명</span>
              <input type="text" name class="form_file_name" v-model="selectedImage.newFileName" />
              <em>.{{ selectedImage.fileExtension }}</em>
            </div>
          </div>
          <div class="tool_body">
            <div class="loading_container" v-show="imageLoading || saveLoading">
              <div class="loader"></div>
							<p style="margin-top: 10px; color: #f7f7f7;" v-if="imageLoading">이미지를 불러오는 중입니다...</p>
							<p style="margin-top: 10px; color: #f7f7f7;" v-else-if="saveLoading">데이터셋 저장 중입니다...</p>
						</div>
						<div id="canvasContainer" class="canvas_container">
              <canvas v-show="selectedImageIndex === index" :id="'labelCanvas' + index" v-bind:key="index" v-for="(canvas, index) in imageList"></canvas>
						</div>
          </div>
          <div class="tool_foot">
            <div class="foot_status" >
              <div class="complete_icon" v-show="statusSaved"></div>
							{{ statusText }}
            </div>
          </div>
        </div>
        <!-- // tool center -->

        <!-- tool right -->
        <div class="tool_box right">
          <div class="tool_label">
            <div class="label_tp">
              <div class="tool_label_tit">라벨 목록</div>
              <div class="label_tool_sel">
                <div class="label_tool add" title="라벨 추가">
                  <span>추가</span>
                </div>
                <div class="label_tool_list">
                  <em class="label_tool select" @click="fnAddLabel('S')" title="Select 툴">
                    <span>Select툴</span>
                  </em>
									<em class="label_tool crop" @click="fnAddLabel('C')" title="Crop 툴">
                    <span>Crop툴</span>
                  </em>
                </div>
              </div>
            </div>
            <div class="label_cont">
              <div data-simplebar class="label_list">
                <ul>
									<li v-bind:key="index" v-for="(label, index) in selectedImage.labelList">
										<div class="label_wrap">
											<div class="label_input">
												<input :id="'labelText' + index" @focus="fnSelectLabel(index)" type="text" :readonly="!label.selected" v-model="label.text" value="" class="label_text_input">
											</div>
											<div class="label_bounding">
												<em @mouseover="fnHoverBbox(index, boxIndex)" @mouseleave="fnLeaveBbox(index, boxIndex)" @click="fnDeleteBbox(index, boxIndex)" v-bind:key="boxIndex" v-for="(bbox, boxIndex) in label.boundingBoxes" :class="bbox.selected ? 'selected' : ''" title="클릭 시 해당 바운딩박스가 삭제됩니다."></em>
											</div>
											<div class="label_control">
												<button type="button" @click="fnCompleteLabel(index)" class="btn_label save" title="완료"><i class="blind">완료</i></button>
												<button type="button" @click="fnDeleteLabel(index)" class="btn_label delete" title="삭제"><i class="blind">삭제</i></button>
											</div>
										</div>
									</li>
								</ul>
              </div>
            </div>
          </div>
        </div>
        <!-- //tool right -->
      </div>
    </div>

    <div class="whole_dim" v-if="imageLoading || saveLoading"></div>
    <datasetSavePopup :eventBusID="'AnnotationToolDatasetSaveEvent'"></datasetSavePopup>
  </div>
</template>

<script>
import $ from 'jquery'
import JSZip from "jszip";
import FileSaver from "file-saver";

async function cropImage (imageFile, options) {
  const img = await loadImage(imageFile);
  return getBase64Image(img, options);
}

function getBase64Image(img, { x, y, width, height }) {
  // Create an empty canvas element
  let canvas = document.createElement("canvas");
  canvas.width = width;
  canvas.height = height;

  // Copy the image contents to the canvas
  let ctx = canvas.getContext("2d");
  ctx.drawImage(img, x, y, width, height, 0, 0, width, height);

  let dataURL = canvas.toDataURL("image/png");

  return dataURL.replace(/^data:image\/(png|jpg);base64,/, "");
}

function loadImage(file) {
  const reader = new FileReader();
  const img = new Image();

  return new Promise((resolve, reject) => {
    reader.onerror = () => {
      reader.abort();
      reject(new DOMException("Loading error."));
    };

    reader.onload = (e) => {
      img.onload = function () {
        resolve(img);
      }
      img.src = e.target.result;
    };
    reader.readAsDataURL(file);
  });
}

export default {
  name: 'annotation-tool',
  components: {},
  data () {
    return {
      datasetInfo: {},
      initCanvasWidth: 0,
      initCanvasHeight: 0,
      windowWidthRatio: 1.0,
      windowHeightRatio: 1.0,
      statusText: '이미지를 선택해주세요.',
      statusSaved: false,
      image: null,
      saveLoading: false,
      imageLoading: false,
      canvas: null,
      context: null,
      mouseIsDown: false,
      canvasInterval: null,
      rectangles: [],
      imageList: [],
      selectedImage: {},
      selectedImageIndex: -1,
      labelList: [],
      selectedLabel: {},
      selectedBbox: {},
      startX: 0,
      startY: 0,
      endX: 0,
      endY: 0,
      canvasScaleFactor: 1.1,
      scaleFactor: null,
      zoomCount: 0,
      zoomInExp: 3,
      zoomOutExp: -3,
      imageMoveX: 0,
      imageMoveY: 0,
      isMoveMode: false,
      newLabelSelectType: {
        selected: true,
        type: 'S',
        text: '',
        boundingBoxes: []
      },
      newLabelCropType: {
        selected: true,
        type: 'C',
        text: '',
        boundingBox: {}
      }
    }
  },
  computed: {
    isAllLabelCompleted () {
      let vm = this
      let completed = true
      if (vm.selectedImage.labelList && vm.selectedImage.labelList.length > 0) {
        vm.selectedImage.labelList.forEach(label => {
          if (label.selected) {
            completed = false
          }
        })
      }

      return completed
    }
  },
  methods: {
    fnGetDatasetSaveInfo (datasetInfo) {
      this.datasetInfo = this.deepCopy(datasetInfo)
      this.fnSaveDataset()
    },
    fnOpenSavePoup () {
      let vm = this

      if (!vm.selectedImage.labelList || vm.selectedImage.labelList.length < 1) {
        vm.alert('저장할 라벨 정보가 없습니다.')
        return
      }

      let existNoLabeled = false
      vm.imageList.some(image => {
        if (!image.labelList || image.labelList.length < 1) {
          existNoLabeled = true
          return true
        }
      })

      if (!vm.isAllLabelCompleted) {
        vm.alert('완료되지 않은 라벨이 있습니다.')
        return
      }

      let continueSave = true
      if (existNoLabeled && !vm.confirm('작업이 진행되지 않은 이미지는 저장되지 않습니다.\n계속 하시겠습니까?')) {
        continueSave = false
      }

      if (!continueSave) {
        return
      }

      this.EventBus.on('AnnotationToolDatasetSaveEvent', this.fnGetDatasetSaveInfo)
      this.EventBus.emit('DatasetSavePopupOpen', {})
    },
    fnOpenFileDialog () {
      $('#imageFiles').click()
    },
    fnSaveDataset () {
      let vm = this
      vm.fnInitScale()
      vm.saveLoading = true

      let zip = new JSZip();
      let zipFilename = `${vm.datasetInfo.fileName}.zip`;

      vm.imageList.forEach((image) => {
        let files = vm._.filter(vm.imageList,
            (targetFile) => {
              return targetFile.newFileName + '.' + targetFile.fileExtension === image.newFileName + '.' + image.fileExtension
            })
        if (files.length > 1) {
          let tempIndex = 1
          files.forEach((sameFile) => {
            if (image.seq !== sameFile.seq) {
              sameFile.newFileName = sameFile.newFileName + '_' + tempIndex
              tempIndex++
            }
          })
        }
      })

      let datasetJson = {}
      datasetJson.images = []
      datasetJson.annotations = []
      datasetJson.cropLabels = []
      let imageIndex = 0
      let annotationIndex = 0

      datasetJson.info = {
        name: this.datasetInfo.name,
        description: '',
        date_created: vm.$moment(new Date()).format('YYYY-MM-DD HH:mm:ss')
      }

      vm.imageList.some(image => {
        if (!image.labelList || image.labelList.length < 1) {
          return false
        }

        let selectLabels = vm._.filter(image.labelList, label => {
          return label.type === 'S'
        })

        if (selectLabels && selectLabels.length > 0) {
          zip.file(`${image.newFileName}.${image.fileExtension}`, image.file);
          // zip.file('aaa.png', cropImage(image.file, { x: 10, y: 10, width: 100, height: 100 }), {base64: true});
        }

        if (selectLabels && selectLabels.length > 0) {
          imageIndex++
          datasetJson.images.push({
            id: imageIndex,
            width: image.width,
            height: image.height,
            file_name: image.newFileName + '.' + image.fileExtension,
            date_captured: vm.$moment(image.file.lastModifiedDate).format('YYYY-MM-DD HH:mm:ss')
          })
        }

        let imageRatio = vm.fnGetRatio(image)
        image.labelList.forEach(label => {
          if (label.type === 'S') {
            label.boundingBoxes.forEach(bbox => {
              let boxX = image.biggerThanContainer
                  ? Math.round(bbox.startX * imageRatio)
                  : Math.round(bbox.startX / imageRatio)
              let boxY = image.biggerThanContainer
                  ? Math.round(bbox.startY * imageRatio)
                  : Math.round(bbox.startY / imageRatio)
              let boxWidth = image.biggerThanContainer
                  ? Math.round(bbox.width * imageRatio)
                  : Math.round(bbox.width / imageRatio)
              let boxHeight = image.biggerThanContainer
                  ? Math.round(bbox.height * imageRatio)
                  : Math.round(bbox.height / imageRatio)

              boxX = Math.round(boxX * vm.windowWidthRatio)
              boxY = Math.round(boxY * vm.windowHeightRatio)
              boxWidth = Math.round(boxWidth * vm.windowWidthRatio)
              boxHeight = Math.round(boxHeight * vm.windowHeightRatio)

              annotationIndex++
              datasetJson.annotations.push({
                id: annotationIndex,
                image_id: imageIndex,
                text: label.text,
                bbox: [boxX, boxY, boxWidth, boxHeight]
              })
            })
          }
        })


        // Crop Type Label에 대한 작업
        let cropLabels = vm._.filter(image.labelList, item => {
          return item.type === 'C'
        })

        let cropIndex = 0
        cropLabels.forEach(cropLabel => {
          let boxX = image.biggerThanContainer
              ? Math.round(cropLabel.boundingBox.startX * imageRatio)
              : Math.round(cropLabel.boundingBox.startX / imageRatio)
          let boxY = image.biggerThanContainer
              ? Math.round(cropLabel.boundingBox.startY * imageRatio)
              : Math.round(cropLabel.boundingBox.startY / imageRatio)
          let boxWidth = image.biggerThanContainer
              ? Math.round(cropLabel.boundingBox.width * imageRatio)
              : Math.round(cropLabel.boundingBox.width / imageRatio)
          let boxHeight = image.biggerThanContainer
              ? Math.round(cropLabel.boundingBox.height * imageRatio)
              : Math.round(cropLabel.boundingBox.height / imageRatio)

          boxX = Math.round(boxX * vm.windowWidthRatio)
          boxY = Math.round(boxY * vm.windowHeightRatio)
          boxWidth = Math.round(boxWidth * vm.windowWidthRatio)
          boxHeight = Math.round(boxHeight * vm.windowHeightRatio)

          cropIndex++
          let croppedImageFileName = `${image.newFileName}_cropped_${cropIndex}.${image.fileExtension}`;

          zip.file(croppedImageFileName,
              cropImage(image.file, { x: boxX, y: boxY, width: boxWidth, height: boxHeight }), {base64: true});

          imageIndex++
          datasetJson.images.push({
            id: imageIndex,
            width: Math.abs(boxWidth),
            height: Math.abs(boxHeight),
            file_name: croppedImageFileName,
            date_captured: vm.$moment(new Date()).format('YYYY-MM-DD HH:mm:ss')
          })

          annotationIndex++
          datasetJson.annotations.push({
            id: annotationIndex,
            image_id: imageIndex,
            text: cropLabel.text,
            bbox: [0, 0, Math.abs(boxWidth), Math.abs(boxHeight)]
          })
        })
      })

      zip.file(`${vm.datasetInfo.name}_info.json`, JSON.stringify(datasetJson, undefined, 4));

      zip.generateAsync({type:"blob"}).then(function(content) {
        FileSaver.saveAs(content, zipFilename);

        vm.statusSaved = true
        vm.statusText = '데이터셋이 저장되었습니다.'

        vm.saveLoading = false

        vm.alert('데이터셋이 저장되었습니다.')
        window.location.reload(true)
      });
    },
    fnLeaveBbox (labelIndex, bboxIndex) {
      let vm = this
      vm.selectedImage.labelList[labelIndex].boundingBoxes[bboxIndex].selected = false
      vm.fnDrawRectangle()
    },
    fnHoverBbox (labelIndex, bboxIndex) {
      let vm = this
      vm.selectedImage.labelList[labelIndex].boundingBoxes.forEach(bbox => {
        bbox.selected = false
      })
      vm.selectedImage.labelList[labelIndex].boundingBoxes[bboxIndex].selected = true

      vm.fnDrawRectangle()
    },
    fnDeleteBbox (labelIndex, bboxIndex) {
      let vm = this
      vm.selectedImage.labelList[labelIndex].boundingBoxes.splice(bboxIndex, 1)

      vm.fnDrawRectangle()
    },
    fnAddLabel (type) {
      let vm = this

      let addable = true
      if (vm.selectedImage.labelList && vm.selectedImage.labelList.length > 0) {
        vm.selectedImage.labelList.forEach(label => {
          if (label.selected) {
            addable = false
          }
        })
      }

      if (!addable) {
        vm.alert('완료되지 않은 라벨이 있습니다.')
        return
      }

      if (type === 'S') {
        vm.selectedLabel = vm.deepCopy(vm.newLabelSelectType)
      } else {
        vm.selectedLabel = vm.deepCopy(vm.newLabelCropType)
      }

      if (
        !vm.selectedImage.labelList ||
        vm.selectedImage.labelList.length < 1
      ) {
        vm.selectedImage.labelList = []
      }
      vm.selectedImage.labelList.push(vm.selectedLabel)

      vm.statusSaved = false
      vm.statusText = '라벨이 추가되었습니다.'

      vm.$forceUpdate()

      vm.$nextTick(() => {
        let lastIndex = vm.selectedImage.labelList.length - 1
        $('#labelText' + lastIndex).focus()

        $('.label_tool_list').slideUp(100)
      })
    },
    fnDeleteLabel (index) {
      let vm = this
      vm.selectedImage.labelList.splice(index, 1)

      vm.$forceUpdate()

      vm.fnDrawRectangle()
    },
    fnSelectLabel (index) {
      let vm = this
      vm.selectedImage.labelList.forEach(label => {
        label.selected = false
        if (label.type === 'S') {
          label.boundingBoxes.forEach(bbox => {
            bbox.selected = false
          })
        } else {
          label.boundingBox.selected = false
        }
      })
      vm.selectedImage.labelList[index].selected = true
      vm.selectedLabel = vm.selectedImage.labelList[index]

      if (vm.selectedLabel.type === 'C') {
        vm.selectedLabel.boundingBox.selected = true
      }

      vm.$nextTick(() => {
        vm.fnDrawRectangle()
      })
    },
    fnCompleteLabel (index) {
      let vm = this

      if (
        !vm.selectedImage.labelList[index].text ||
        vm.selectedImage.labelList[index].text.length < 1
      ) {
        vm.alert('텍스트 입력 후 완료가 가능합니다.')
        return
      }

      if (vm.selectedImage.labelList[index].type === 'S') {
        if (
          !vm.selectedImage.labelList[index].boundingBoxes ||
          vm.selectedImage.labelList[index].boundingBoxes.length < 1
        ) {
          vm.alert('하나 이상의 바운딩박스 지정 후 완료가 가능합니다.')
          return
        }

        vm.selectedImage.labelList[index].selected = false
        vm.selectedImage.labelList[index].boundingBoxes.forEach(bbox => {
          bbox.selected = false
        })
      } else {
        if (!vm.selectedImage.labelList[index].boundingBox.width) {
          vm.alert('하나 이상의 바운딩박스 지정 후 완료가 가능합니다.')
          return
        }

        vm.selectedImage.labelList[index].selected = false
        vm.selectedImage.labelList[index].boundingBox.selected = false
      }
      vm.selectedLabel = vm.deepCopy({})
      vm.selectedBbox = vm.deepCopy({})

      vm.statusSaved = true
      vm.statusText = '라벨정보가 저장되었습니다.'

      vm.$forceUpdate()
      vm.$nextTick(() => {
        vm.fnDrawRectangle()
      })
    },
    fnChangeModeMove () {
      let vm = this
      // 이동 모드 전환
      if (vm.zoomCount < 1) {
        vm.isMoveMode = false
        $('#labelCanvas' + vm.selectedImageIndex).css('cursor', 'default')
        return
      }

      vm.isMoveMode = !vm.isMoveMode

      vm.$forceUpdate()

      vm.$nextTick(() => {
        if (vm.isMoveMode) {
          $('#labelCanvas' + vm.selectedImageIndex).css('cursor', '-webkit-grab')
        } else {
          $('#labelCanvas' + vm.selectedImageIndex).css('cursor', 'default')
        }
      })
    },
    fnZoomIn () {
      let vm = this
      // canvas 확대
      if (Object.keys(vm.selectedImage).length < 1) {
        return
      }

      if (Math.abs(vm.zoomCount) > 4) {
        return
      }

      vm.zoomCount++
      vm.scaleFactor = Math.pow(vm.canvasScaleFactor, vm.zoomInExp)
      vm.selectedImage.context.clearRect(0, 0, vm.selectedImage.width, vm.selectedImage.height)
      vm.selectedImage.context.scale(vm.scaleFactor, vm.scaleFactor)
      vm.fnDrawRectangle()
    },
    fnZoomOut () {
      let vm = this
      // canvas 축소
      if (Object.keys(vm.selectedImage).length < 1) {
        return
      }

      if (vm.zoomCount === 0) {
        return
      }
      vm.zoomCount--
      vm.scaleFactor = Math.pow(vm.canvasScaleFactor, vm.zoomOutExp)
      vm.selectedImage.context.clearRect(0, 0, vm.selectedImage.width, vm.selectedImage.height)
      vm.selectedImage.context.scale(vm.scaleFactor, vm.scaleFactor)
      if (vm.zoomCount === 0) {
        vm.imageMoveX = 0
        vm.imageMoveY = 0
        vm.selectedImage.context.setTransform(1, 0, 0, 1, vm.imageMoveX, vm.imageMoveY)
        vm.fnChangeModeMove()
      }

      vm.fnDrawRectangle()
    },
    fnInitScale () {
      let vm = this
      // 확대/축소 원상태로 복구
      if (vm.zoomCount === 0) {
        return
      }

      let isZoomed = vm.zoomCount > 0

      vm.imageMoveX = 0
      vm.imageMoveY = 0

      for (let i = 0; i < Math.abs(vm.zoomCount); i++) {
        vm.scaleFactor = Math.pow(
          vm.canvasScaleFactor,
          isZoomed ? vm.zoomOutExp : vm.zoomInExp
        )
        vm.selectedImage.context.scale(vm.scaleFactor, vm.scaleFactor)
      }

      vm.selectedImage.context.setTransform(1, 0, 0, 1, vm.imageMoveX, vm.imageMoveY)
      vm.selectedImage.context.clearRect(0, 0, vm.selectedImage.width, vm.selectedImage.height)
      vm.zoomCount = 0
      vm.fnChangeModeMove()
      vm.fnDrawRectangle()
    },
    fnSetCanvasEvent () {
      let vm = this
      vm.selectedImage.canvas.addEventListener('mousedown', vm.fnMouseDown, false)
      vm.selectedImage.canvas.addEventListener('mousemove', vm.fnMouseMove, false)
      vm.selectedImage.canvas.addEventListener('mouseup', vm.fnMouseUp, false)
    },
    fnMouseDown () {
      let vm = this

      vm.mouseIsDown = true

      if (vm.isMoveMode) {
        $('#labelCanvas' + vm.selectedImageIndex).css('cursor', '-webkit-grabbing')
      } else {
        if (Object.keys(vm.selectedLabel).length < 1) {
          return
        }

        vm.selectedBbox = { selected: true }
        if (vm.selectedLabel.type === 'S') {
          vm.selectedLabel.boundingBoxes.push(vm.selectedBbox)
        } else {
          vm.selectedLabel.boundingBox = vm.selectedBbox
        }

        vm.selectedImage.context.lineWidth = 2
        vm.selectedImage.context.strokeStyle = '#f33'

        let rect = vm.selectedImage.canvas.getBoundingClientRect()
        vm.startX = vm.endX = event.clientX - rect.left
        vm.startY = vm.endY = event.clientY - rect.top

        vm.startX = vm.endX = vm.endX + vm.imageMoveX * -1
        vm.startY = vm.endY = vm.endY + vm.imageMoveY * -1

        if (vm.zoomCount !== 0) {
          let scaleFactor = 0
          let isZoomed = vm.zoomCount > 0
          scaleFactor = Math.pow(
            vm.canvasScaleFactor,
            isZoomed ? vm.zoomOutExp : vm.zoomInExp
          )
          for (let i = 0; i < Math.abs(vm.zoomCount); i++) {
            vm.startX = vm.endX = vm.endX * scaleFactor
            vm.startY = vm.endY = vm.endY * scaleFactor
          }
        }

        vm.fnDrawRectangle()

        vm.canvasInterval = setInterval(() => {
          vm.fnDrawRectangle()
        }, 5)
      }
    },
    fnMouseMove (event) {
      let vm = this

      let isZoomed = vm.zoomCount > 0
      if (vm.mouseIsDown) {
        if (vm.isMoveMode) {
          let moveScaleFactor = 0
          moveScaleFactor = Math.pow(vm.canvasScaleFactor, isZoomed ? vm.zoomInExp * vm.zoomCount : vm.zoomOutExp * Math.abs(vm.zoomCount))

          vm.imageMoveX += event.movementX
          vm.imageMoveY += event.movementY

          if (event.movementX > 0 && event.movementY > 0) {
            if (vm.imageMoveX > 0 && vm.imageMoveY > 0) {
              vm.imageMoveX = 0
              vm.imageMoveY = 0
            }
          }

          if (event.movementX > 0 && vm.imageMoveX > 0) {
            vm.imageMoveX = 0
          }
          if (event.movementY > 0 && vm.imageMoveY > 0) {
            vm.imageMoveY = 0
          }

          let imageRealX = 0
          let imageRealY = 0
          imageRealX = Math.round(vm.imageMoveX / ((moveScaleFactor - 1) * -1))
          imageRealY = Math.round(vm.imageMoveY / ((moveScaleFactor - 1) * -1))

          if (vm.imageMoveX < 0 && vm.imageMoveY < 0) {
            if (imageRealX > vm.selectedImage.canvas.width && imageRealY > vm.selectedImage.canvas.height) {
              vm.imageMoveX = Math.round(vm.selectedImage.canvas.width * ((moveScaleFactor - 1) * -1))
              vm.imageMoveY = Math.round(vm.selectedImage.canvas.height * ((moveScaleFactor - 1) * -1))
            }
          }
          if (imageRealX > vm.selectedImage.canvas.width) {
            vm.imageMoveX = Math.round(vm.selectedImage.canvas.width * ((moveScaleFactor - 1) * -1))
          }
          if (imageRealY > vm.selectedImage.canvas.height) {
            vm.imageMoveY = Math.round(vm.selectedImage.canvas.height * ((moveScaleFactor - 1) * -1))
          }

          vm.selectedImage.context.clearRect(0, 0, vm.selectedImage.width, vm.selectedImage.height)
          vm.selectedImage.context.setTransform(moveScaleFactor, 0, 0, moveScaleFactor, vm.imageMoveX, vm.imageMoveY)
          vm.fnDrawRectangle()
        } else {
          if (Object.keys(vm.selectedLabel).length < 1) {
            return
          }

          let rect = vm.selectedImage.canvas.getBoundingClientRect()
          vm.endX = event.clientX - rect.left
          vm.endY = event.clientY - rect.top

          vm.endX = vm.endX + vm.imageMoveX * -1
          vm.endY = vm.endY + vm.imageMoveY * -1

          if (vm.zoomCount !== 0) {
            let scaleFactor = 0
            scaleFactor = Math.pow(
              vm.canvasScaleFactor,
              isZoomed ? vm.zoomOutExp : vm.zoomInExp
            )
            for (let i = 0; i < Math.abs(vm.zoomCount); i++) {
              vm.endX = vm.endX * scaleFactor
              vm.endY = vm.endY * scaleFactor
            }
          }
        }
      }
    },
    fnMouseUp () {
      let vm = this
      if (vm.mouseIsDown) {
        vm.mouseIsDown = false

        if (vm.isMoveMode) {
          $('#labelCanvas' + vm.selectedImageIndex).css('cursor', '-webkit-grab')
        } else {
          if (vm.startX > vm.endX) {
            let tmpX = vm.startX
            vm.startX = vm.endX
            vm.endX = tmpX
          }
          if (vm.startY > vm.endY) {
            let tmpY = vm.startY
            vm.startY = vm.endY
            vm.endY = tmpY
          }

          let width = vm.endX - vm.startX
          let height = vm.endY - vm.startY

          if (width < 5 || height < 5) {
            if (vm.selectedLabel.type === 'S') {
              vm.selectedLabel.boundingBoxes.splice(vm.selectedLabel.boundingBoxes.length - 1, 1)
            }
            return
          }

          if (vm.selectedLabel.type === 'S') {
            vm.selectedLabel.boundingBoxes[vm.selectedLabel.boundingBoxes.length - 1] = {
              startX: vm.startX,
              startY: vm.startY,
              endX: vm.endX,
              endY: vm.endY,
              width: vm.endX - vm.startX,
              height: vm.endY - vm.startY,
              selected: false
            }
          } else {
            vm.selectedBbox.selected = false
            vm.selectedLabel.boundingBox = {
              startX: vm.startX,
              startY: vm.startY,
              endX: vm.endX,
              endY: vm.endY,
              width: vm.endX - vm.startX,
              height: vm.endY - vm.startY,
              selected: false
            }
          }
          vm.$forceUpdate()
          vm.fnDrawRectangle()
          clearInterval(vm.canvasInterval)
        }
      }
    },
    fnDrawRectangle () {
      let vm = this
      vm.selectedImage.context.drawImage(vm.image, 0, 0, vm.image.width, vm.image.height, 0, 0, vm.selectedImage.canvas.width, vm.selectedImage.canvas.height)

      if (vm.selectedImage.labelList && vm.selectedImage.labelList.length > 0) {
        vm.selectedImage.labelList.forEach(label => {
          if (label.type === 'S') {
            label.boundingBoxes.forEach(bbox => {
              if (bbox.selected) {
                vm.selectedImage.context.strokeStyle = '#f33'
              } else {
                vm.selectedImage.context.strokeStyle = '#111'
              }

              vm.selectedImage.context.setLineDash([])

              vm.selectedImage.context.beginPath()
              vm.selectedImage.context.rect(
                bbox.startX * vm.windowWidthRatio,
                bbox.startY * vm.windowHeightRatio,
                bbox.width * vm.windowWidthRatio,
                bbox.height * vm.windowHeightRatio
              )
              vm.selectedImage.context.closePath()
              vm.selectedImage.context.stroke()
            })
          } else {
            let bbox = label.boundingBox
            if (bbox.selected) {
              vm.selectedImage.context.strokeStyle = '#f33'
            } else {
              vm.selectedImage.context.strokeStyle = '#111'
            }

            vm.selectedImage.context.globalAlpha = 0.1
            vm.selectedImage.context.fillRect(
              bbox.startX * vm.windowWidthRatio,
              bbox.startY * vm.windowHeightRatio,
              bbox.width * vm.windowWidthRatio,
              bbox.height * vm.windowHeightRatio
            )
            vm.selectedImage.context.globalAlpha = 1.0
            vm.selectedImage.context.setLineDash([5, 3])

            vm.selectedImage.context.beginPath()
            vm.selectedImage.context.rect(
              bbox.startX * vm.windowWidthRatio,
              bbox.startY * vm.windowHeightRatio,
              bbox.width * vm.windowWidthRatio,
              bbox.height * vm.windowHeightRatio
            )
            vm.selectedImage.context.closePath()
            vm.selectedImage.context.stroke()
          }
        })
      }

      if (vm.mouseIsDown && !vm.isMoveMode) {
        vm.selectedImage.context.strokeStyle = '#f33'
        let width = vm.endX - vm.startX
        let height = vm.endY - vm.startY

        if (vm.selectedLabel.type === 'C') {
          vm.selectedImage.context.globalAlpha = 0.1
          vm.selectedImage.context.fillRect(
            vm.startX,
            vm.startY,
            width,
            height
          )
          vm.selectedImage.context.globalAlpha = 1.0
          vm.selectedImage.context.setLineDash([5, 3])
        } else {
          vm.selectedImage.context.setLineDash([])
        }
        vm.selectedImage.context.beginPath()
        vm.selectedImage.context.rect(vm.startX, vm.startY, width, height)
        vm.selectedImage.context.stroke()
      }
    },
    fnGetRatio (image) {
      let canvasContainerWidth = $('#canvasContainer').width()
      let canvasContainerHeight = $('#canvasContainer').height()

      if (
        canvasContainerWidth < image.realImage.naturalWidth &&
        canvasContainerHeight < image.realImage.naturalHeight
      ) {
        if (image.realImage.naturalWidth > image.realImage.naturalHeight) {
          return (
            parseFloat(image.realImage.naturalWidth) /
            parseFloat(canvasContainerWidth)
          )
        } else if (
          image.realImage.naturalHeight > image.realImage.naturalWidth
        ) {
          return (
            parseFloat(image.realImage.naturalHeight) /
            parseFloat(canvasContainerHeight)
          )
        }
      } else if (canvasContainerWidth < image.realImage.naturalWidth) {
        return (
          parseFloat(image.realImage.naturalWidth) /
          parseFloat(canvasContainerWidth)
        )
      } else if (canvasContainerHeight < image.realImage.naturalHeight) {
        return (
          parseFloat(image.realImage.naturalHeight) /
          parseFloat(canvasContainerHeight)
        )
      }

      if (
        canvasContainerWidth > image.realImage.naturalWidth ||
        canvasContainerHeight > image.realImage.naturalHeight
      ) {
        let imageWidth = image.realImage.naturalWidth
        let imageHeight = image.realImage.naturalHeight
        let ratioCount = 0

        while (imageWidth < canvasContainerWidth - 10 && imageHeight < canvasContainerHeight - 10) {
          ratioCount++
          imageWidth = imageWidth * 1.01
          imageHeight = imageHeight * 1.01
        }

        let ratio = 1.01
        ratioCount = ratioCount - 1
        for (let i = 0; i < ratioCount; i++) {
          ratio = ratio * 1.01
        }

        return ratio
      }
    },
    fnSetCanvasSize () {
      let vm = this

      let canvasContainerWidth = $('#canvasContainer').width()
      let canvasContainerHeight = $('#canvasContainer').height()
      let ratio = 0

      if (
        canvasContainerWidth < vm.image.naturalWidth &&
        canvasContainerHeight < vm.image.naturalHeight
      ) {
        vm.selectedImage.biggerThanContainer = true
        // 이미지 가로x세로가 캔버스 가로x세로 보다 큰 경우
        if (vm.image.naturalWidth > vm.image.naturalHeight) {
          // 가로가 더 큰 경우
          ratio = parseFloat(canvasContainerWidth) / parseFloat(vm.image.naturalWidth)
          vm.selectedImage.canvas.width = Math.round(vm.image.naturalWidth * ratio) - 5
          vm.selectedImage.canvas.height = Math.round(vm.image.naturalHeight * ratio) - 5
          return
        } else if (vm.image.naturalHeight > vm.image.naturalWidth) {
          // 세로가 더 큰 경우
          ratio = parseFloat(canvasContainerHeight) / parseFloat(vm.image.naturalHeight)
          vm.selectedImage.canvas.width = Math.round(vm.image.naturalWidth * ratio) - 5
          vm.selectedImage.canvas.height = Math.round(vm.image.naturalHeight * ratio) - 5
          return
        }
      } else if (canvasContainerWidth < vm.image.naturalWidth) {
        vm.selectedImage.biggerThanContainer = true
        // 이미지 가로가 캔버스 가로 보다 큰 경우
        ratio =
          parseFloat(canvasContainerWidth) / parseFloat(vm.image.naturalWidth)
        vm.selectedImage.canvas.width = Math.round(vm.image.naturalWidth * ratio) - 5
        vm.selectedImage.canvas.height = Math.round(vm.image.naturalHeight * ratio) - 5
        return
      } else if (canvasContainerHeight < vm.image.naturalHeight) {
        vm.selectedImage.biggerThanContainer = true
        // 이미지 세로가 캔버스 세로 보다 큰 경우
        ratio = parseFloat(canvasContainerHeight) / parseFloat(vm.image.naturalHeight)
        vm.selectedImage.canvas.width = Math.round(vm.image.naturalWidth * ratio) - 5
        vm.selectedImage.canvas.height = Math.round(vm.image.naturalHeight * ratio) - 5
        return
      }

      if (
        canvasContainerWidth > vm.image.naturalWidth ||
        canvasContainerHeight > vm.image.naturalHeight
      ) {
        vm.selectedImage.biggerThanContainer = false
        // 이미지 가로x세로가 캔버스 가로x세로 보다 작은 경우
        let imageWidth = vm.image.naturalWidth
        let imageHeight = vm.image.naturalHeight

        while (imageWidth < canvasContainerWidth - 10 && imageHeight < canvasContainerHeight - 10) {
          imageWidth = imageWidth * 1.01
          imageHeight = imageHeight * 1.01
        }

        vm.selectedImage.canvas.width = imageWidth
        vm.selectedImage.canvas.height = imageHeight
      }

      // $(vm.canvas).css('margin-top', (canvasContainerHeight - vm.selectedImage.canvas.height) / 2)
    },
    fnSelectImage (index) {
      let vm = this

      if (!vm.isAllLabelCompleted) {
        vm.alert('완료되지 않은 라벨이 있습니다.')
        return
      }

      if (vm.selectedImage.canvas) {
        vm.fnInitScale()
      }

      vm.imageList.forEach(item => {
        item.selected = false
        if (item.labelList && item.labelList.length > 0) {
          item.labelList.forEach(label => {
            label.selected = false
          })
        }
      })
      vm.imageList[index].selected = true

      vm.selectedImageIndex = index
      vm.selectedImage = vm.imageList[index]

      vm.statusSaved = false
      vm.statusText = '이미지가 선택되었습니다. 라벨을 추가해주세요.'

      vm.$forceUpdate()

      // Canvas 설정 (Image 포함)
      if (vm.selectedImage.canvas) {
        $(vm.selectedImage.canvas).off()
      } else {
        vm.selectedImage.canvas = $('#labelCanvas' + index)[0]
        vm.selectedImage.context = vm.selectedImage.canvas.getContext('2d')
      }

      vm.image = new Image()

      vm.image.onload = () => {
        vm.selectedImage.canvas.width = vm.image.naturalWidth + 1
        vm.selectedImage.canvas.height = vm.image.naturalHeight + 1

        vm.fnSetCanvasSize()

        vm.selectedImage.realImage = vm.image

        vm.initCanvasWidth = vm.selectedImage.canvas.width
        vm.initCanvasHeight = vm.selectedImage.canvas.height

        vm.selectedImage.context.drawImage(vm.image, 0, 0, vm.image.width, vm.image.height, 0, 0, vm.selectedImage.canvas.width, vm.selectedImage.canvas.height)

        vm.selectedImage.width = vm.image.naturalWidth
        vm.selectedImage.height = vm.image.naturalHeight

        vm.fnSetCanvasEvent()
        vm.imageLoading = false

        vm.fnDrawRectangle()
      }
      vm.imageLoading = true
      vm.$forceUpdate()
      vm.image.src = vm.selectedImage.imageUrl
    },
    fnLoadImage () {
      $('#imageFiles').click()
    },
    fnImageLoaded (e) {
      let vm = this

      if ((e.target.files.length + vm.imageList.length) > 10) {
        vm.alert('이미지는 10개 이하로만 등록 가능합니다.')
        return
      }

      let imageFile
      let fileName
      let fileExtension
      let imageListIndex = vm.imageList.length - 1
      for (let i = 0; i < e.target.files.length; i++) {
        imageFile = e.target.files[i]
        fileName = e.target.files[i].name.substring(0, e.target.files[i].name.lastIndexOf('.'))
        fileExtension = e.target.files[i].name.substring(e.target.files[i].name.lastIndexOf('.') + 1)

        vm.imageList.push({
          seq: (imageListIndex + 1),
          file: imageFile,
          newFileName: fileName,
          fileName: fileName,
          path: imageFile.path,
          fileExtension: fileExtension,
          selected: false
        })
      }

      vm.$nextTick(() => {
        for (let i = 0; i < e.target.files.length; i++) {
          let imageUrl = URL.createObjectURL(e.target.files[i])
          $('#image' + ((imageListIndex + 1) + i)).attr('src', imageUrl)
          vm.imageList[(imageListIndex + 1) + i].imageUrl = imageUrl
        }
      })
    },
    fnSetCanvasResize () {
      let vm = this
      if (vm.selectedImage.canvas) {
        let resizedCanvasWidth = $(vm.selectedImage.canvas).width()
        let resizedCanvasHeight = $(vm.selectedImage.canvas).height()

        if (vm.initCanvasWidth < 0 && vm.initCanvasHeight < 0) {
          return
        }

        vm.windowWidthRatio = resizedCanvasWidth / vm.initCanvasWidth
        vm.windowHeightRatio = resizedCanvasHeight / vm.initCanvasHeight

        if (vm.image) {
          vm.fnSetCanvasSize()
          vm.selectedImage.context.drawImage(
            vm.image,
            0,
            0,
            vm.image.width,
            vm.image.height,
            0,
            0,
            vm.selectedImage.canvas.width,
            vm.selectedImage.canvas.height
          )
          vm.fnDrawRectangle()
        }
      }
    }
  },
  mounted () {
    let vm = this

    vm.EventBus.on('GO_BOUNDING_BOX', () => {
      vm.EventBus.emit('GO_BOUNDING_BOX_RESULT', false)
    })
    vm.EventBus.on('GO_VERIFICATION', () => {
      if (vm.imageList && vm.imageList.length > 0) {
        let continueMove = vm.confirm('작업 중인 데이터가 모두 삭제됩니다. 이동 하시겠습니까?')
        vm.EventBus.emit('GO_VERIFICATION_RESULT', continueMove)
      } else {
        vm.EventBus.emit('GO_VERIFICATION_RESULT', true)
      }
    })
    vm.EventBus.on('GO_CAPTURE', () => {
      if (vm.imageList && vm.imageList.length > 0) {
        let continueMove = vm.confirm('작업 중인 데이터가 모두 삭제됩니다. 이동 하시겠습니까?')
        vm.EventBus.emit('GO_CAPTURE_RESULT', continueMove)
      } else {
        vm.EventBus.emit('GO_CAPTURE_RESULT', true)
      }
    })

    $('.label_tool_sel').hover(
      () => {
        if (Object.keys(vm.selectedImage).length < 1) {
          return
        }
        $('.label_tool_list').slideDown(100)
      },
      () => {
        $('.label_tool_list').slideUp(100)
      }
    )

    $('.tool_opt_list').hover(
      () => {
        if (Object.keys(vm.selectedImage).length < 1) {
          return
        }
        $('.tool_zoom_items').slideDown(100)
      },
      () => {
        $('.tool_zoom_items').slideUp(100)
      }
    )

    window.addEventListener('resize', () => {
      vm.fnSetCanvasResize()
    })

    window.addEventListener('maximize', () => {
      vm.fnSetCanvasResize()
    })

    window.addEventListener('unmaximize', () => {
      vm.fnSetCanvasResize()
    })

    window.addEventListener('minimize', () => {
      vm.fnSetCanvasResize()
    })

    window.addEventListener('restore', () => {
      vm.fnSetCanvasResize()
    })
  }
}
</script>

<style>
input[type="file"] {
  display: none;
}
</style>
