import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import VueMoment from 'vue-moment'
import VueLodash from 'vue-lodash'
import lodash from 'lodash'

import axios from 'axios'
import { library, dom } from '@fortawesome/fontawesome-svg-core'
// import { faCloudUploadAlt } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { fas } from '@fortawesome/free-solid-svg-icons'

// 상단 Header Component
import Header from './components/Header'
// Dataset Save Popup Component
import DatasetSavePopup from './components/DatasetSavePopup'

import 'simplebar'
import 'simplebar/dist/simplebar.css'
import './assets/css/font.css'
import './assets/css/reset.css'
import './assets/css/tool_open.css'

// header component 선언
Vue.component('commonHeader', Header)
// dataset save popup component 선언
Vue.component('datasetSavePopup', DatasetSavePopup)

// EventBus 설정
Vue.prototype.EventBus = new Vue(
  {
    methods: {
      on (name, listener) {
        this.$off(name).$on(name, listener)
      },
      emit (name, ...param) {
        this.$emit(name, ...param)
      }
    }
  }
)

Vue.prototype.alert = (msg) => {
  alert(msg, 'Annotation Tool')
}
Vue.prototype.confirm = (msg) => {
  return confirm(msg, 'Annotation Tool')
}
Vue.prototype.deepCopy = (target) => {
  return JSON.parse(JSON.stringify(target))
}

library.add(fas)
// library.add(faCloudUploadAlt)
dom.watch()

// if (!process.env.IS_WEB) Vue.use(require('vue-electron'))
Vue.http = Vue.prototype.$http = axios

Vue.component('font-awesome-icon', FontAwesomeIcon)

Vue.use(VueMoment)

// name is optional
Vue.use(VueLodash, { lodash: lodash })

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
