<template>
  <!-- popup -->
  <div id="layerbox" class="layerpop pop_dataset">
    <div class="layerpop_area">
      <div class="pop_title">
        <span>데이터셋</span>
      </div>
      <div class="pop_body">
        <ul class="dataset_info">
          <li>
            <input type="text" id="name" placeholder="이름" v-model="datasetSaveInfo.name" />
          </li>
          <li>
            <input type="text" id="fileName" placeholder="파일명" v-model="datasetSaveInfo.fileName" />
          </li>
        </ul>
      </div>
      <div class="pop_footer">
        <a @click.prevent="fnPopupSave" href="javascript:;" class="btn_to_pop">저장</a>
        <a @click.prevent="fnPopupClose" href="javascript:;" class="pop_close btn_to_pop" id="layerbox_close">닫기</a>
      </div>
    </div>
  </div>
  <!-- // popup -->
</template>

<script>
import $ from 'jquery'

export default {
  name: 'DatasetSavePopup',
  props: ['eventBusID'],
  data () {
    return {
      datasetSaveInfo: {
        name: '',
        fileName: '',
        savePath: ''
      }
    }
  },
  methods: {
    fnPopupSave () {
      if (!this.datasetSaveInfo.name) {
        this.alert('데이터셋 이름을 입력해주세요.')
        $('#name').focus()
        return
      }
      if (!this.datasetSaveInfo.fileName) {
        this.alert('데이터셋 파일 명을 입력해주세요.')
        $('#fileName').focus()
        return
      }

      this.EventBus.emit(this.eventBusID, this.datasetSaveInfo)
      this.fnPopupClose()
    },
    fnPopupOpen () {
      $('.layerpop').css('position', 'absolute')
      // 영역 가운에데 레이어를 뛰우기 위해 위치 계산
      $('.layerpop').css('top', (($(window).height() - $('.layerpop').outerHeight()) / 2) + $(window).scrollTop())
      $('.layerpop').css('left', (($(window).width() - $('.layerpop').outerWidth()) / 2) + $(window).scrollLeft())
      // $('.layerpop').draggable();
      $('#layerbox').show()
      $('#name').focus()
    },
    fnPopupClose () {
      $('#layerbox').hide()
      $('#mask').hide()

      this.datasetSaveInfo = {
        name: '',
        fileName: '',
        savePath: ''
      }
    }
  },
  mounted () {
    this.EventBus.on('DatasetSavePopupOpen', this.fnPopupOpen)
  }
}
</script>
